.project .content {
  display: flex;
}

.project .carousel-container {
  display: flex;
  flex-direction: column;
  margin: 10px 0px;
  padding: 20px 0px;
  position: relative;
  height: 400px;
  margin-top: 100px;
  overflow: hidden;
  background-color: #999999;
}

.project .carousel-container h2 {
  margin: auto;
  margin-bottom: 10px;
}

.project .images-container {
  margin: auto;
  width: 650px;
  overflow: hidden;
  border-radius: 20px;
}

.project .carousel-images {
  display: flex;
}

.project .carousel-images img {
  width: 650px;
}

.project .carousel-image {
  flex-shrink: 0;
  width: 30%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.project .carousel-image.active {
  opacity: 1;
}

.project .carousel-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.project .carousel-button {
  width: 10px; 
  height: 10px;
  border-radius: 50%;
  margin: 5px;
  background-color: #ffffff;
  cursor: pointer;
}

.project .carousel-button.active {
  background-color: #666666;
}

.project .page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  margin: auto;
  width: 800px;
}

.project .page .title {
  width: 1000px;
  text-align: center;
  padding: 2px 8px;
}

.project .page .type1 {
  display: flex;
  align-items: center;
  text-align: left;
  flex-direction: row;
  margin: 20px;
  width: 800px;
}

.project .page .type1 img {
  width: 325px;
  margin: 10px 25px;
}

.project .page .type2 {
  display: flex;
  align-items: center;
  text-align: right;
  flex-direction: row-reverse;
  margin: 20px;
  width: 800px;
}

.project .page .type2 img {
  width: 325px;
  margin: 10px 25px;
}

.project .page .type3 {
  text-align: center;
  margin: 20px;
  width: 800px;
}

.project .page .type3 img {
  margin: 5px;
  width: 180px;
}

.project .page .type4 {
  text-align: center;
  margin-top: 10px;
}

.project .page .type4 img {
  margin: 10px;
  width: 600px;
}

.project .page h2 {
  width: 800px;

  background-color: #cccccc;
}