.period {
  width: 1000px;
  margin: auto;
  margin-top: 100px;
}

.period .sidebar {
  position: fixed;
  align-items: center;
  width: 180px;
  border-right: 5px solid #000000;
  border-image: linear-gradient(to top, #333333, #cccccc);
  border-image-slice: 1;
}

.period .caption {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  border: 2px solid #000000;
  border-radius: 5px;
}

.period .sub {
  font-size: 8px;
  margin-left: 5px;
  height: 1px;
}

.period :link {
  text-decoration: none;
  color: #000000;
}

.period :visited {
  text-decoration: none;
  color: #000000;
}

.period :link:hover {
  color: #ffffff;
}

.period :visited:hover {
  color: #ffffff;
}

.period .content {
  display: flex;
  margin-top: 10px;
  margin-left: 240px;
  width: 780px;
}

.period .page .title {
  padding: 2px 8px;
}

.period .page .detail {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
}

.period .page .type1 {
  display: flex;
  align-items: center;
  text-align: left;
  flex-direction: row;
  margin: 20px;
  width: 720px;
}

.period .page .type1 img {
  width: 325px;
  margin: 10px 25px;
}

.period .page .type2 {
  display: flex;
  align-items: center;
  text-align: right;
  flex-direction: row-reverse;
  margin: 20px;
  width: 720px;
}

.period .page .type2 img {
  width: 325px;
  margin: 10px 25px;
}

.period .page .type3 {
  text-align: center;
  margin: 20px;
  width: 800px;
}

.period .page .type3 img {
  margin: 5px;
  width: 180px;
}

.period .page .type4 {
  text-align: center;
  margin-top: 50px;
}

.period .page .type4 img {
  margin: 5px;
  width: 180px;
}
