.main {
  display: flex;
  flex-direction: column;
  align-content: center;
}

.sample {
  display: flex;
  margin: auto;
  margin-top: 100px;
  border-radius: 10px;
  width: 1200px;
  background-color: #333333;
}

.sample .content {
  padding: 0px 50px;
}

.sample .top {
  display: flex;
  align-items: flex-end;
  padding-bottom: 40px;
  border-bottom: 2px solid #ffffff;
  width: 600px;
  height: 80px;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
}

.sample .top img {
  margin: 0px 10px;
  width: 50px;
  height: 50px;
}

.sample .mark {
  display: contents;
  font-size: 30px;
}

.sample .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  border-radius: 50%;
  width: 300px;
  height: 300px;
  background: url("../../assets/images/icon/icon_normal.png") no-repeat center;
  background-size: 300px;
}

.sample .detail {
  display: flex;
  font-size: 20px;
  color: #ffffff;
}

.sample .detail ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  justify-content: space-between;
}

.sample .detail li {
  display: flex;
  margin-bottom: 12px;
}

.sample .thead {
  width: 80px;
  font-weight: bold;
}

.view {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-top: 100px;
  border: 2px #000000 solid;
  background-color: #999999;
  width: 800px;
}

.view table {
  margin: auto;
  border-collapse: collapse;
  margin: 5px;
  border: 1px #000000 solid;
  width: 800px;
  background-color: #eeeeee;
}

.view caption {
  margin: 0px;
  font-size: x-large;
  color: #000000;
}

.view .icon {
  text-align: center;
  width: 180px;
  padding: 0px 10px;
}

.view .icon img {
  width: 180px;
}

.view tr {
  text-align: left;
}

.view tr th {
  padding-left: 5px;
}

.view tr td {
  padding-left: 5px;
}

.view .mark {
  color: red;
  font-size: large;
}

.hintBar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 9999;
}

.hintBar span {
  font-size: 20px;
}

.hintBar.show {
  opacity: 1;
}