.header {
  display: flex;
  justify-content: right;
  align-items: center;
  margin: auto;
  padding: 10px;
  position: fixed;
  width: 1200px;
  top: 0%;
  left: 50%;
  transform: translate(-50%, 0%);
  background-color: #666666;
}

.header li {
  display: inline;
  margin-right: 36px;
  font-weight: bold;
  font-size: 24px;
}

.header :link {
  text-decoration: none;
  color: #61dafb;
}

.header :visited {
  text-decoration: none;
  color: #61dafb;
}

.header :link:hover {
  color: #ffffff;
}

.header :visited:hover {
  color: #ffffff;
}
