.license {
  width: 1000px;
  margin: auto;
  margin-top: 100px;
}

.license .group {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
  margin: auto;
  margin-bottom: 10px;
  border-radius: 20px;
  width: 100%;
}

.license .group h1 {
  width: 100%;
  border-radius: 10px;
  color: white;
}

.license .group img {
  width: 280px;
}

.license .group img:hover {
  transform: scale(1.2) translate(0%, -20px);
}

.license .card {
  margin: 5px;
  color: white;
}
